:root {
  --basic-type: #666666;
  --dark-background: #1b2d37;
  --contrast-type: #ffffff;
  --contrast-border: var(--contrast-type);
  --main-theme: #00b2ff; }

@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("PT Mono"), local("PTMono"), url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), local("Roboto-Condensed"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed Bold"), local("Roboto-Condensed-Bold"), url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff"); }

img {
  max-width: 100%;
  height: auto; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: var(--basic-type);
  font-size: 16px;
  height: 100%;
  scroll-behavior: smooth; }

.page__body {
  background-color: var(--clear-background);
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: center;
  line-height: 22px;
  overflow: auto; }

.page__button {
  display: inline-block;
  cursor: pointer;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 5px;
  padding: 14px 26px;
  padding-top: 13px;
  color: var(--contrast-type);
  border: 1px solid var(--contrast-border); }
  @media (max-width: 1023px) {
    .page__button {
      padding: 18px 30px; } }

.page-header {
  padding-top: 28px;
  padding-bottom: 20px;
  min-width: 180px; }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 74.3%;
    margin: 0 auto;
    min-width: 950px; }
  .page-header__logo {
    margin-right: 30px;
